.title {
    margin-bottom: 10px;
    display: flex;
    justify-content: center;
}

.square {
    background: white;
    border: 1px solid gray;
    float: left;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    width: 80px;
    height: 80px;
    text-align: center;
    font-size: 40px;
    font-weight: bold;
    line-height: 80px;
}

.square:focus {
    outline: none;
}

/* Board */
.border-row::after {
    clear: both;
    content: "";
    display: table;
}

/* Game */
.game {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    flex-direction: row;
    margin: 0 auto;
}

/* Game Info */
.ul {
    padding-left: 30px;
}

.move-list {
    display: flex;
    list-style: none;
}

.game-info {
    margin-left: 20px;
}

.status {
    margin-bottom: 10px;
}